@import "https://cdnjs.cloudflare.com/ajax/libs/modern-normalize/2.0.0/modern-normalize.min.css";
@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
header, footer, body, span, div, nav, svg, img, ul, li, h1, h2, h3, h4, h5, h6, a, p {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  text-decoration: none;
  list-style: none;
  display: block;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  background-color: var(--background);
}

.disabled {
  display: none;
}

:root {
  --orange: #f87719;
  --grey: #b7b7b7;
  --white: #fff;
  --black: #000;
  --background: #111;
  --secondary-text-grey: #f8f8f8;
  --secondary-black-grey: #282828;
  --grey-white-theme: #595959;
  color: var(--white);
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.header {
  width: 1280px;
  height: 60px;
  background-color: var(--background);
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 32px;
  display: flex;
}

.header__logo-box {
  align-items: center;
  gap: 8px;
  display: flex;
}

.header__nav-list {
  gap: 50px;
  display: flex;
}

.header__nav-item-link {
  color: var(--white);
}

.header__contrast-btn-box {
  width: 60px;
  height: 24px;
  background-color: var(--orange);
  border-radius: 194px;
}

.hero {
  width: 1280px;
  min-width: 500px;
  background-color: var(--background);
  margin: 0 auto;
  padding-left: 135px;
  display: flex;
}

.hero__movie-desc {
  margin: auto 0;
}

.hero__rating {
  display: flex;
}

.movielist {
  width: 1280px;
  min-width: 500px;
  background-color: var(--background);
  margin: 0 auto;
  padding: 0 32px;
}

.movielist__nav-tends-box {
  width: 100%;
  justify-content: space-between;
  padding: 0 32px;
  display: flex;
}

.movielist__movie-card-list {
  justify-content: space-between;
  display: flex;
}

.movielist__movie-itemt {
  height: 574px;
  width: 395px;
  border: 1px solid #fff;
  position: relative;
}

.movielist__movie-image {
  height: 100%;
  width: 100%;
}

.movielist__movie-description {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.movielist__movie-details {
  gap: 10px;
  display: flex;
}

.movielist__rating-list {
  display: flex;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.fetch-api__movie-draft {
  flex-wrap: wrap;
  display: flex;
}

.fetch-api__movie-draft-item {
  max-width: 200px;
  height: 100%;
}

.upcoming {
  width: 1280px;
  min-width: 500px;
  background-color: var(--background);
  margin: 0 auto;
  padding: 0 32px;
}

.upcoming__film-box {
  display: flex;
}

.upcoming__film-details-box {
  margin: auto 0;
}

.upcoming__film-details {
  gap: 58px;
  display: flex;
}

.upcoming__film-rateing {
  gap: 5px;
  display: flex;
}

.footer {
  width: 1280px;
  background-color: var(--background);
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 0 32px;
  display: flex;
}

.footer__box {
  align-items: center;
  gap: 10px;
  padding: 100px 0 80px;
  display: flex;
}

.footer__paragraf {
  gap: 5px;
  display: flex;
}

.btn {
  cursor: pointer;
  border: none;
  border-radius: 74px;
  margin: 0 auto;
  padding: 12px 24px;
  font-family: inherit;
  font-weight: 500;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.btn__orange-gradient {
  color: var(--background);
  background: linear-gradient(145deg, #ffc226 0%, #f84119 100%);
}

.btn__black {
  background-color: var(--background);
  color: var(--white);
  border: 1px var(--orange) solid;
}

.btn.btn__orange-gradient:hover, .btn.btn__orange-gradient:active {
  background-color: var(--white);
  color: var(--orange);
  background: none;
  border: 1px solid red;
}

.btn.btn__black:hover, .btn.btn__black:active {
  background-color: var(--white);
  color: var(--orange);
  border: none;
}

.btn__small {
  font-size: 12px;
  line-height: 1.33333;
}

@media (max-width: 767.999px) {
  .container {
    max-width: 320px;
    margin: 0 auto;
    padding: 0 20px;
  }
}

@media (min-width: 768px) and (max-width: 1280px) {
  .container {
    max-width: 768px;
    margin: 0 auto;
    padding: 0 32px;
  }

  .btn__big {
    font-size: 14px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    margin: 0 auto;
  }

  .btn__big {
    font-size: 18px;
    line-height: 1.55556;
  }

  .btn__semi-medium {
    font-size: 16px;
    line-height: 1.5;
  }

  .btn__medium {
    font-size: 14px;
    line-height: 1.14286;
  }
}

